.page {
  margin: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}
.page__container {
  min-width: 320px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.page__content {
  background-color: #ffffff;
}
.content__section {
  width: calc(100% - 40px);
  margin: 0 auto;
}
.section__heading {
  margin: 0;
  padding-right: 20px;
  padding-left: 20px;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 660px) {
  .section__heading {
    padding-right: 20px;
    padding-left: 20px;
    font-size: 18px;
  }
}
@media (min-width: 1200px) {
  .section__heading {
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    padding-right: 130px;
    padding-left: 130px;
  }
}
.section__heading--place--index {
  font-size: 24px;
  line-height: 1.2;
  font-weight: 200;
}
@media (min-width: 660px) {
  .section__heading--place--index {
    font-size: 40px;
    line-height: 1.35;
  }
}
@media (min-width: 1200px) {
  .section__heading--place--index {
    font-size: 50px;
  }
}
.section__heading--place--photo {
  padding-right: calc(20px + 15px);
  padding-left: calc(20px + 15px);
}
@media (min-width: 660px) {
  .section__heading--place--photo {
    padding-right: calc(20px + 25px);
    padding-left: calc(20px + 25px);
  }
}
.section__container {
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
}
