}
.navigation__toggle {
  padding: 0;
  background-color: transparent;
  color: transparent;
  border: none;
  outline: none;
  position: absolute;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation--closed .navigation__item {
  display: none;
}
@media (min-width: 1200px) {
  .navigation--closed .navigation__item {
    display: block;
  }
}
.navigation--closed .navigation__toggle {
  width: 50px;
  height: 24px;
  top: 21px;
  right: 20px;
  color: #ffffff;
  transition: color 0.5s ease;
}
.navigation--closed .navigation__toggle:hover {
  color: #d22856;
}
.navigation--closed .navigation__toggle:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 660px) {
  .navigation--closed .navigation__toggle {
    top: 44px;
  }
}
.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 4px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
  transition: box-shadow 0.5s, background-color 0.5s ease;
}
.navigation--closed .navigation__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.navigation--closed .navigation__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.5);
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.5), 0 20px 0 0 rgba(210, 40, 86, 0.5);
}
.navigation--opened .navigation__list {
  border-top: 1px solid #000000;
}
@media (min-width: 1200px) {
  .navigation--opened .navigation__list {
    border-top: none;
  }
}
.navigation--opened .navigation__toggle {
  width: 23px;
  height: 23px;
  top: 24px;
  right: 23px;
}
@media (min-width: 660px) {
  .navigation--opened .navigation__toggle {
    top: 44px;
  }
}
.navigation--opened .navigation__toggle::before,
.navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  left: 0;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  transition: background-color 0.5s ease;
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
.navigation--opened .navigation__toggle:hover::before,
.navigation--opened .navigation__toggle:hover::after {
  background-color: #d22856;
}
.navigation--opened .navigation__toggle:active::before,
.navigation--opened .navigation__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.5);
}
.header--nojs .navigation__toggle {
  display: none;
}
.header--nojs .navigation__item {
  display: block;
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .navigation__list {
    margin: auto 0;
    display: grid;
    grid-template-columns: repeat(4, max-content);
    gap: 20px 79px;
  }
}
.navigation__item {
  padding: 15px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000000;
  text-align: center;
}
@media (min-width: 1200px) {
