    padding: 0;
    border: none;
  }
}
.navigation__link {
  padding: 0 20px;
  color: inherit;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.5s ease;
}
.navigation__link[href]:hover {
  color: #d22856;
}
.navigation__link[href]:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1200px) {
  .navigation__link {
    padding: 0;
    line-height: 18px;
    font-weight: 400;
    text-transform: none;
  }
  .navigation__link--current {
    position: relative;
  }
  .navigation__link--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -11px;
    left: 0;
    height: 2px;
    background-color: #ffffff;
  }
  .navigation__link[href]:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
